import {Box, Paper, Stack, ThemeProvider} from "@mui/material";
import {Link, useNavigate} from "react-router-dom";
import {cuteTheme} from "../../../context/ThemeContext";
import ParticleArea from "../effect/ParticleArea";
import {useAppSelector} from "../../../api/redux/hooks";
import {selectEnableParticle} from "../../../api/redux/slice/SettingSlice";
import {Button, Typography} from "../../../component/wrapper/MuiWrappers";
import {PAGE_MIN_HEIGHT} from "./Portal";
import "./Notfound.css"
import React from "react";

const NotFound = () => {
  const navigate = useNavigate();
  const enableParticle = useAppSelector(selectEnableParticle);
  return (
      <Stack
          direction="row"
          justifyContent="center"
          alignItems="center"
          sx={{width: "100%", height: PAGE_MIN_HEIGHT}}
      >
        <ThemeProvider theme={cuteTheme}>
          <Paper component={Stack} direction={"column"} spacing={6} justifyContent="center"
                 alignItems={"center"}
                 sx={{width: "100%", height: "75svh",}} elevation={2}
                 style={{backgroundColor: "#80cbc488"}}>

            <Typography variant={"h2"}>{"Dimension 404"}</Typography>
            <section className="error-container">
              <span className="four"><span className="screen-reader-text">4</span></span>
              <span className="zero"><span className="screen-reader-text">0</span></span>
              <span className="four"><span className="screen-reader-text">4</span></span>
            </section>
            <Box style={{zIndex: -1, position: "absolute"}}>
              {enableParticle && <ParticleArea id={"404particle"}/>}
            </Box>

            <Button className={"link-container"} variant={"contained"} color={"primary"}
                    component={Link} to={"/"} sx={{
              boxShadow: 0,
              backgroundColor: "#de7e85",
            }}>{"Return To My Dimension"}</Button>
          </Paper>
        </ThemeProvider>
      </Stack>
  );
}

export default React.memo(NotFound);