import * as React from 'react';
import {useSearchParams} from "react-router-dom";
import {queryCustomerProfile} from "./service/CustomerService";
import {Divider, IconButton, Stack, StackProps} from "@mui/material";
import ProfileAvatar from "./component/ProfileAvatar";
import {Tooltip, Typography} from "../../component/wrapper/MuiWrappers";
import {RoleIcon} from "./component/RoleIcon";
import {UserRole} from "../../proto/framework/SecurityMessage";
import EditIcon from '@mui/icons-material/Edit';
import EmailIcon from '@mui/icons-material/Email';
import {useMutation, useQuery, useQueryClient} from "@tanstack/react-query";
import {DialogNames, openDialogWithPromise} from "../../component/dialog/DialogManager";
import NotFound from "../website/page/NotFound";
import LoadingScreen from "../website/page/LoadingScreen";
import {toColor} from "../../api/util/ColorUtil";


const CustomerProfileWidget = ({...rest}: StackProps) => {
  const [searchParams, setSearchParams] = useSearchParams();
  const queryClient = useQueryClient();

  // const [customerProfile, setCustomerProfile] = React.useState<CustomerProfile | null>(null);
  // let customerProfile: CustomerProfile | null = null;
  const userId = searchParams.get('id');
  const isEditMode = userId === null;

  const {data: customerProfile, isLoading} = useQuery({
    queryFn: () => queryCustomerProfile(userId),
    queryKey: ["query", {userId}],
    // staleTime: 1000 * 60 * 0, // 1 minute not refresh, default 0
    // gcTime: DEFAULT_CACHE_TIME, // time before the cache is garbage collected
  });

  const {mutateAsync: mutateCustomerName} = useMutation({
    // onSuccess: () => {
    // reload the data
    //    queryClient.invalidateQueries({
    //      queryKey: ["query"],
    //    });
    // },
    // gcTime: DEFAULT_CACHE_TIME, already set in queryClient
    onSuccess: (data) => {
      queryClient.setQueryData(["query", {userId}], {...customerProfile, name: data});
    },
    mutationFn: async () => {
      const result = await openDialogWithPromise(DialogNames.EditCustomerNameDialog, {username: customerProfile?.name});
      if (result.success) {
        return result.data;
      } else {
        return customerProfile?.name;
      }
    }
  });

  const {mutateAsync: mutateEmail} = useMutation({
    onSuccess: (data) => {
      queryClient.setQueryData(["query", {userId}], {...customerProfile, email: data});
    },
    mutationFn: async () => {
      const result = await openDialogWithPromise(DialogNames.EditEmailDialog);
      if (result.success) {
        return result.data;
      } else {
        return customerProfile?.email;
      }
    }
  });

  const {mutateAsync: mutateDescription} = useMutation({
    onSuccess: (data) => {
      queryClient.setQueryData(["query", {userId}], {...customerProfile, description: data});
    },
    mutationFn: async () => {
      const result = await openDialogWithPromise(DialogNames.EditCustomerDescriptionDialog, {
        description: customerProfile?.description,
      });
      if (result.success) {
        return result.data;
      } else {
        return customerProfile?.description;
      }
    }
  });

  const {mutateAsync: mutateAvatar} = useMutation({
    onSuccess: (data) => {
      queryClient.setQueryData(["query", {userId}], {...customerProfile, avatarUrl: data});
    },
    mutationFn: async () => {
      const result = await openDialogWithPromise(DialogNames.EditAvatarDialog);
      if (result.success) {
        return result.data;
      } else {
        return customerProfile?.avatarUrl;
      }
    }
  });

  // useEffect(() => {
  //   queryCustomerProfile(userId).then(res => {
  //     setCustomerProfile(res);
  //   }).catch(e => {
  //   })
  // }, [searchParams]);


  if (isLoading) {
    return <LoadingScreen/>;
  }

  return (
      customerProfile === undefined ? <NotFound></NotFound> :
          <Stack direction={"column"} justifyContent={"center"} alignItems={"center"}
                 spacing={2} marginTop={"32px"} {...rest}>
            <ProfileAvatar title={customerProfile.name + "(" + customerProfile.id + ")"}
                           avatarUrl={customerProfile.avatarUrl}
                           sx={{
                             bgcolor: toColor(customerProfile.name),
                             width: "96px",
                             height: "96px",
                             fontSize: "50px",
                           }}
                           style={{
                             cursor: "pointer",
                             width: "96px",
                             height: "96px",
                             fontSize: "50px",
                             border: "5px solid",
                             borderColor: "#4285F4 #DB4437 #F4B400 #0F9D58",
                           }}
                           onClick={() => {
                             mutateAvatar();
                           }}
            />
            <Divider sx={{width: '30%'}}> <RoleIcon userRole={customerProfile.role}
                                                    className={"awesome-icon"}/> </Divider>
            <Stack direction={"row"} alignItems={"center"} spacing={1}>
              <Typography variant={"h5"} enableTranslate={false}>{customerProfile.name}</Typography>
              {isEditMode &&
                  <Tooltip title={"Edit"}><IconButton
                      onClick={() => mutateCustomerName()}><EditIcon/></IconButton></Tooltip>}
            </Stack>

            {isEditMode && customerProfile.role === UserRole.GUEST && <Typography variant={"body2"}>
              {"You are current a guest, adding an email or sign in with a third party account would upgrade your account to a registered user."}
            </Typography>}
            <Stack direction={"row"} alignItems={"center"} spacing={1}>
              <EmailIcon sx={{fontSize: "30px"}}/>
              <Typography variant={"body1"}>
                {customerProfile.email}</Typography> {isEditMode &&
                <Tooltip title={"Edit"}><IconButton onClick={
                  () => mutateEmail()
                }><EditIcon/></IconButton></Tooltip>}
            </Stack>

            <Stack direction={"row"} alignItems={"center"} spacing={1}>
              <Typography variant={"body1"} enableTranslate={false}>
                {customerProfile.description?.length === 0 ? (isEditMode ? "Announcement" : "") : customerProfile.description}</Typography> {isEditMode &&
                <Tooltip title={"Edit"}><IconButton onClick={() => mutateDescription()
                }><EditIcon/></IconButton></Tooltip>}
            </Stack>
            <div> {customerProfile?.avatarUrl}</div>

          </Stack>
  );
};

export default React.memo(CustomerProfileWidget);