// @flow
import * as React from 'react';
import {UserRole} from "../../../proto/framework/SecurityMessage";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faPerson, faUser, faUserTie} from '@fortawesome/free-solid-svg-icons'
import {useTranslation} from "react-i18next";

type RoleIconProp = {
  userRole?: UserRole,
  fontSize?: string,
  className?: string,
}
export const RoleIcon = ({userRole = UserRole.GUEST, ...rest}: RoleIconProp) => {
  const {t} = useTranslation();
  switch (userRole) {
    case UserRole.ADMIN:
      return <FontAwesomeIcon icon={faUserTie} {...rest} title={t("Admin")}/>;
    case UserRole.USER:
      return <FontAwesomeIcon icon={faUser} {...rest} title={t("User")}/>;
    case UserRole.GUEST:
    default:
      return <FontAwesomeIcon icon={faPerson} {...rest} title={t("Guest")}/>;
  }
};